.TimeSelector {
  width: 90%;
  margin: 5px 5%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.TimeSelector.error {
  padding-left: 5px;
  border-left: red solid 6px;
}

.TimeSelector button {
  border: none;
}

.TimeSelector button {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 24%;
  height: 40px;
  margin: 5px 1px;
  border-radius: 5px;
}
.TimeSelector button:disabled {
  background-color: rgb(218, 218, 218);
  color: gray;
}

.ts-selected {
  -webkit-transition: all 3ms;
  -o-transition: all 3ms;
  transition: all 3ms;
  font-weight: 700;
  -webkit-box-shadow: rgb(17, 173, 77) 0px 5px;
          box-shadow: rgb(17, 173, 77) 0px 5px;
  -webkit-transform: translateY(-5px);
      -ms-transform: translateY(-5px);
          transform: translateY(-5px);
}
.TimeSelector button:enabled.ts-selected {
  background-color: rgb(44, 226, 99);
}
.TimeSelector button:enabled {
  background-color: rgb(86, 202, 106);
  cursor: pointer;
  color: black;
}
.TimeSelector button:enabled:hover {
  background-color: rgb(80, 219, 103);
}

.TimeSelector button:active {
  border: none;
}



