.FormTurnosLayout {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
}
.FormTurnosLayout .inputs {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: row;
      flex-direction: row;
}
.FormTurnosLayout .inputsText {
  display: -ms-flexbox;
  display: flex;
  width: 50%;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
}
.FormTurnosLayout input {
  padding: 5px;
  margin: 10px;
}
.FormTurnosLayout label {
  font-size: 0.7em;
  padding: 0px 15px;
}
.InputTime {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-direction: row;
      flex-direction: row;
}
.InputTime input {
  max-width: 40%;
}
input.error {
  border: solid 2px red;
}
.FormTurnosLayout select {
  height: 2.5em;
  margin: 10px;
  padding: 5px;
  min-width: 100px;
}

.FormTurnosLayout h3,
.FormTurnosLayout h4 {
  padding: 0px;
  margin: 5px;
  text-align: center;
}
.FormTurnosLayout .btn {
  min-width: 50%;
}
.formButtonContainer {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin: var(--main-margin) 0px;
}
.formTitle b {
  color: var(--colorBaseDark);
  font-size: 1.2em;
  text-decoration: underline;
}
.formTitle {
  width: 100%;
  display: block;
  text-align: left;
  background-color: rgb(221, 221, 221);
  padding: 0.1em 0;
  border-radius: 5px;
  margin-bottom: 20px;
}
.formTitle p {
  margin-left: 20px;
}
.formErrors {
  width: 90%;
  padding-left: 10%;
  display: block;
  text-align: left;
  background-color: rgb(221, 221, 221);
  border-radius: 5px;
  color: var(--colorSecundario);
}
.fcol-1 {
  width: 100%;
  display: inline-block;
}
.fcol-2 {
  width: 50%;
  float: left;
}
@media only screen and (max-width: 1024px) {
  .fcol-2 {
    width: calc(100%);
  }
}



